// components/AdminStateExplorer.tsx

import { useState } from "react";
// FIX: Removed useGlobeStore import
import GlobeStates from "./GlobeStates";
import { JURISDICTIONS, DEFAULT_JURISDICTION } from "../lib/jurisdictions";

// Helper component for the selector menu
const JurisdictionSelector = ({
  selectedRegion,
  setSelectedRegion,
}: {
  selectedRegion: string;
  setSelectedRegion: (region: string) => void;
}) => (
  <div className="flex justify-center p-4 bg-slate-800 rounded-lg shadow-xl z-20 relative">
    <select
      className="block w-full max-w-sm rounded-md border-0 py-1.5 pl-3 pr-10 text-slate-100 bg-slate-700 ring-1 ring-inset ring-slate-600 focus:ring-2 focus:ring-emerald-500 sm:text-sm sm:leading-6"
      value={selectedRegion}
      onChange={(e) => setSelectedRegion(e.target.value)}
    >
      <option value="ALL">All Regions</option>
      <option value="north_america">North America</option>
      <option value="europe">Europe</option>
    </select>
  </div>
);

export default function AdminStateExplorer() {
  // FIX: State is now managed here instead of a missing external store
  const [selectedStateId, setSelectedStateId] = useState<string | null>(null);
  
  // Default to North America to show more than just the US initially if desired, 
  // or use DEFAULT_JURISDICTION.region
  const [selectedRegion, setSelectedRegion] = useState<string>("north_america");

  return (
    <div className="flex flex-col h-screen overflow-hidden bg-slate-900">
      
      <div className="flex-shrink-0 z-20">
        <JurisdictionSelector
          selectedRegion={selectedRegion}
          setSelectedRegion={setSelectedRegion}
        />
      </div>

      <main className="flex-1 flex min-h-0 relative p-4">
        <div className="relative h-full w-full mx-auto max-w-7xl">
          {/* FIX: Passing state down as props */}
          <GlobeStates 
            region={selectedRegion} 
            selectedStateId={selectedStateId} 
            onStateSelect={setSelectedStateId}
          />
          
          <div className="pointer-events-none absolute inset-0 rounded-lg shadow-[0_0_150px_100px_rgba(2,_6,_23,_1)] [transform:translateZ(-100px)]"></div>
          
          {selectedStateId && (
            <div className="absolute top-4 right-4 z-10 p-4 bg-slate-800/90 backdrop-blur text-white rounded-lg shadow-xl border border-slate-700">
              <p className="font-semibold text-xs uppercase tracking-wider text-slate-400">Selected Jurisdiction</p>
              <p className="text-xl font-bold text-emerald-400 mt-1">{selectedStateId}</p>
            </div>
          )}
        </div>
      </main>

      <footer className="flex-shrink-0 p-2 text-center text-xs text-slate-500 bg-slate-900 border-t border-slate-800 z-20">
        Cartfax Admin Explorer - Viewing {selectedRegion === 'ALL' ? 'Global' : selectedRegion} Region
      </footer>
    </div>
  );
}